$base-size: 16px;

// Converts dimensions in pixels into rems, based on $base-size
@function px2rem ($target, $context: $base-size) {
  @return ($target / $context) * 1rem;
}

@mixin alpheios-border {
  border: 1px solid var(--alpheios-color-neutral-dark);
  border-radius: px2rem(10px);
}

@mixin alpheios-interactive {
  color: var(--alpheios-color-vivid);
  cursor: pointer;

  &:hover,
  &:active {
    text-decoration: underline;
  }
}