.alpheios-popup {
  display: flex;
  flex-direction: column;
  min-width: 300px;
  min-height: 150px;
  z-index: 1000;
  position: fixed;
  left: 200px;
  top: 100px;
  box-sizing: border-box;
  overflow: auto;
  border: 1px solid var(--alpheios-color-neutral-dark);
  border-radius: 0.625rem;
}

.alpheios-popup__header {
  display: flex;
  justify-content: space-between;
  height: 3.5rem;
  background: var(--alpheios-color-muted);
}

.alpheios-popup__logo {
  height: 1.75rem;
  width: auto;
  margin: 0.875rem 0 0 0.875rem;
}

.alpheios-popup__close-btn {
  width: 5rem;
  height: 100%;
  cursor: pointer;
  fill: var(--alpheios-color-neutral-lightest);
  stroke: var(--alpheios-color-neutral-lightest);
  stroke-width: 2.5;
}
.alpheios-popup__close-btn svg {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  left: 1rem;
  width: 2.75rem;
  height: auto;
}
.alpheios-popup__close-btn:hover, .alpheios-popup__close-btn:focus {
  fill: var(--alpheios-color-bright-hover);
  stroke: var(--alpheios-color-bright-hover);
  background: var(--alpheios-color-muted-hover);
}
.alpheios-popup__close-btn:active {
  fill: var(--alpheios-color-bright-pressed);
  stroke: var(--alpheios-color-bright-pressed);
  background: var(--alpheios-color-muted-pressed);
}

.alpheios-popup__body {
  display: flex;
  flex-direction: column;
  padding: 1.25rem;
}

.alpheios-popup__toolbar {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.625rem;
}

.alpheios-popup__toolbar-text {
  padding-right: 1.25rem;
}

.alpheios-popup__toolbar-text[lang=ara] {
  padding: 0px 20px 0px 20px;
  /* the arabic amiri font does not like the top padding */
}

h3.alpheios-popup__toolbar-selection {
  display: inline-block;
}

.alpheios-popup__toolbar-buttons {
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
}

.alpheios-popup__toolbar-button {
  margin: 0 0 0.625rem 0.625rem;
}

.alpheios-popup__content {
  padding: 1.25rem;
  border: 1px solid var(--alpheios-color-neutral-dark);
  border-radius: 0.625rem;
}

.alpheios-popup__providers {
  margin-left: 2.5rem;
}

.alpheios-popup__providers-title {
  font-weight: 700;
  color: var(--alpheios-color-neutral-dark);
}

a.alpheios-popup__providers-link {
  display: inline-block;
  margin-bottom: 0.375rem;
  font-weight: 700;
}

.alpheios-popup__definitions--placeholder {
  padding: 0.625rem 0;
}

.alpheios-popup__providers-item {
  color: var(--alpheios-color-neutral-dark);
}

.alpheios-popup__notifications {
  display: none;
  position: relative;
  padding: 0.625rem 1.25rem;
  background: #BCE5F0;
  flex: 0 0 3.75rem;
  overflow: hidden;
}

.alpheios-popup__notifications-close-btn {
  position: absolute;
  right: 0.3125rem;
  top: 0.3125rem;
  display: block;
  width: 1.25rem;
  height: 1.25rem;
  margin: 0;
  cursor: pointer;
  fill: #D1D1D0;
  stroke: #D1D1D0;
}

.alpheios-popup__notifications-close-btn:hover,
.alpheios-popup__notifications-close-btn:focus {
  fill: #5BC8DC;
  stroke: #5BC8DC;
}

[data-notification-visible=true] .alpheios-popup__notifications {
  display: block;
}

.alpheios-popup__notifications--lang-switcher {
  font-size: 12px;
  float: right;
  margin: -1.25rem 0.625rem 0 0;
  display: inline-block;
}

.alpheios-popup__notifications--lang-switcher .alpheios-select {
  width: 7.5rem;
  height: 1.5625rem;
}

.alpheios-popup__notifications--important {
  background: #73CDDE;
}